.list-container {
    position: fixed;
    left: 50%;
    top: 50%;
    width: 300px;
    margin-left: -730px;
    background-color: #fff;
    box-shadow: 0px 0px 10px 3px #e2e2e2;
    border-radius: 10px;
}

.list_xiaoxin {
    width: 30px;
    height: 30px;
}

p {
    text-align: center;
    font-size: 22px;
    color: #8f0053;
}

.listheader-container {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    margin: 10px;
    background-color: #ffffff;
    text-align: center;
    line-height: 50px;
    color: #353535;
    font-size: 16px;
    box-shadow: 0px 0px 10px 2px #e0dfdf;
    border-radius: 8px;
    box-sizing: border-box;
}

.listheader-index {
    width: 15%;
    height: 100%;
}

.listheader-time {
    width: 35%;
    height: 100%;
    color: #4d00b3;
}

.listheader-winner {
    width: 20%;
    height: 100%;
    color: #cc0000;
}

.listheader-review {
    width: 15%;
    height: 100%;
    color: #00705e;
}

.list-scrollContainer {
    width: 100%;
    margin-bottom: 5px;
    max-height: 500px;
    overflow: scroll;
}

.list-scrollContainer::-webkit-scrollbar {
    display: none;
}

.no-history {
    position: relative;
    height: 50px;
    margin: 10px;
    background-color: #f5f6f5;
    text-align: center;
    line-height: 50px;
    color: #353535;
    font-size: 16px;
    box-shadow: 0px 0px 10px 2px #e0dfdf;
    border-radius: 8px;
    box-sizing: border-box;
    transition: all 0.4s;
}

.no-history:hover {
    background-color: #fff;
    transform: scale(1.05);
}