.app {
  position: relative;
  width: 100%;
  height: 100vh;
  margin: 0 auto;
}

div {
  cursor: pointer;
}

.clock {
  position: absolute;
  left: 1%;
  top: 1%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 59px;
  font-size: 24px;
  text-align: center;
  color: brown;
  line-height: 59px;
}

.xiaoxin {
  width: 74px;
  height: 59px;
}

.countdown {
  position: absolute;
  right: 1%;
  top: 1%;
  width: 300px;
  height: 60px;
  font-size: 26px;
  text-align: center;
  color: brown;
  line-height: 60px;
}