.board {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -390px;
    margin-left: -390px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 780px;
    height: 780px;
    border: 2px solid #181717;
    background-color: #ffba82;
    border-radius: 10px;
    box-sizing: border-box;
}

.center,
.left-top,
.right-top,
.left-bottom,
.right-bottom {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 14px;
    height: 14px;
    background-color: #181717;
    border-radius: 50%;
}

.center {
    margin-left: -7px;
    margin-top: -7px;
}

.left-top {
    margin-left: -247px;
    margin-top: -247px;
}

.left-bottom {
    margin-left: -247px;
    margin-top: 233px;
}

.right-top {
    margin-left: 233px;
    margin-top: -247px;
}

.right-bottom {
    margin-left: 233px;
    margin-top: 233px;
}

.mask-disable {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0);
}