* {
    box-sizing: border-box;
}

.container {
    position: relative;
    width: 40px;
    height: 40px;
    background-image: url(../../../public/grid.png);
}

.cicle {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 26px;
    height: 26px;
    margin-top: -13px;
    margin-left: -13px;
    box-shadow: 0px 0px 2px 2px #5f5d5d;
    border-radius: 50%;
    z-index: 2;
}